.test{
  color: red;
}

.menuButton {
  margin-right: 20px,
}

.title {
  flex-grow: 1,
}

.home-container {
  color: #1d2f54;
  padding: 30px 0;

  .welcome-container {
    display: flex;
    padding: 15px;
    margin-bottom: 20px;
    align-items: center;
    justify-content: space-between;
    font-size: 20px;
    font-weight: bold;
    background-color: #f5f9fe;

    .img-back{
      border: none;
      border-radius: 50%;
      background-color: #E0EEFF;
      padding: 7px;

      .logo {
        height: 40px;

        img{
          height: 100%;
        }
      }
    }
  }

  

  .menu-item{
    text-align: center;
    padding: 50px;
    min-height: 50px;
  }
}

.offerContainer{
  width: 90%;
  margin: 0 20px;
  overflow: auto;

  .offers{
    display: flex;
    flex-direction: row;
  
    .offer-tile{
      flex: 1;
      min-width: 300px;
      height: 150px;
      margin-right: 20px;
    }

    .offer-bg-1{
      background: rgba(201,223,155, 0.46);
    }

    .offer-bg-2{
      background: #FDB6B6;
    }
  }

}

.light-green {
  background-color: #E9F9F2 !important;
}

.light-yellow {
  background-color: #FFF6E5 !important;
}

.light-pink {
  background-color: #FFEFEF !important;
}

.light-blue {
  background-color: #F5F9FE !important;
}

.light-grey {
  background-color: #FAFAFA !important;
}

